<template>
  <div>
    <el-menu style="" class="leftNav" unique-opened>
      <el-submenu
        :index="index + ''"
        v-for="(item, index) in leftNavData"
        :key="index"
      >
        <template slot="title">{{ index }}</template>
        <el-submenu
          :index="index2 + ''"
          v-for="(item2, index2) in item"
          :key="index2"
        >
          <template slot="title">{{ index2 }}</template>
          <el-menu-item
            :index="index2 + item3"
            v-for="(item3, index3) in item2"
            :key="index3"
            @click="NavClick(index, index2, item3)"
            >{{ item3 }}</el-menu-item
          >
        </el-submenu>
      </el-submenu>
    </el-menu>
    <div style="margin-left: 13.5vw">
      <h3 style="padding-top: 2vh; color: white">
        亚马逊客户消息列表（站内邮件）<el-button
          type="primary"
          @click="showMail()"
          >回复买家消息</el-button
        >
      </h3>
      <table class="BuyerNewsTable">
        <thead>
          <tr>
            <th>序号</th>
            <th>站点</th>
            <th>国家</th>
            <th>订单编号</th>
            <th>问题类型</th>
            <th>处理时间</th>
            <th>处理人</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in tableData" :key="index">
            <td>{{ index + 1 + (page - 1) * 50 }}</td>
            <td>{{ item.site }}</td>
            <td>{{ item.country }}</td>
            <td>{{ item.order_number }}</td>
            <td>{{ item.problem_type }}</td>
            <td>{{ item.processing_time }}</td>
            <td>{{ item.dispose_person }}</td>
          </tr>
        </tbody>
      </table>
      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="page"
        layout="prev, pager, next, jumper"
        :total="total"
        class="pagetotle"
        style="margin: 1vh 0 0 35vw"
      >
      </el-pagination>
      <div class="bottomBox">
        <div style="position: absolute; top: 43vh; left: 41vw">
          <i
            class="el-icon-right"
            style="font-size: 2rem; color: #66b1ff; opacity: 0.5"
          ></i>
        </div>
        <div style="position: absolute; top: 72vh; left: 41vw">
          <i
            class="el-icon-right"
            style="font-size: 2rem; color: #66b1ff; opacity: 0.5"
          ></i>
        </div>
        <h4 style="margin: 2vh">回复站内邮件</h4>
        <div style="margin: 2vh 0 2vh 10vh">
          订单编号：<el-input
            v-model="replyOrderNo"
            placeholder="请输入订单编号"
            style="width: 20vw"
          ></el-input
          ><el-button
            type="primary"
            @click="getDataById()"
            style="margin-left: 1vw"
            >查 看</el-button
          >
        </div>
        <div>
          <span style="margin: 10vh 5vh 10vh 10vh"
            >站点：<el-input
              placeholder="请输入订单编号获取"
              v-model="replySite"
              style="width: 10vw"
              :readonly="editArr['replySite']"
            ></el-input
          ></span>
          <span style="margin: 5vh"
            >国家：<el-input
              placeholder="请输入订单编号获取"
              v-model="replyCountry"
              style="width: 10vw"
              :readonly="editArr['replyCountry']"
            ></el-input
          ></span>
          <span style="margin: 5vh"
            >平台：<el-input
              placeholder="请输入订单编号获取"
              v-model="replyPlatform"
              style="width: 10vw"
              :readonly="editArr['replyPlatform']"
            ></el-input
          ></span>
          <span style="margin: 5vh"
            >语言：<el-input
              placeholder="请输入订单编号获取"
              v-model="replyLanguage"
              style="width: 10vw"
              :readonly="editArr['replyLanguage']"
            ></el-input
          ></span>
        </div>
        <div style="margin: 2vh 0 0 10vh">
          商品及其数量：
          <el-input
            placeholder="请输入订单编号获取"
            v-model="replyGoodsNo"
            style="width: 30vw"
            :readonly="editArr['replyGoodsNo']"
          ></el-input>
        </div>
        <div style="margin: 2vh 0 0 10vh; display: flex">
          <div style="flex: 1">
            买家消息内容：
            <el-input
              type="textarea"
              placeholder="买家消息内容翻译"
              v-model="replyBuysText"
              maxlength="9999"
              show-word-limit
              style="width: 20vw"
              :rows="15"
            >
            </el-input>
          </div>
          <div style="flex: 1">
            <el-button
              type="primary"
              style="margin: 17.5vh 0 0 5vw; width: 10vw"
              @click="interpret"
              :disabled="interpretBtn"
              >匹配并翻译</el-button
            >
          </div>
          <div style="flex: 2">
            <el-input
              type="textarea"
              placeholder="买家消息翻译"
              v-model="replyTranslateChinese"
              maxlength="9999"
              show-word-limit
              style="width: 35vw"
              :rows="15"
              readonly
            >
            </el-input>
          </div>
        </div>
        <div style="margin: 2vh 0 0 10vh; display: flex">
          <div style="flex: 4; border: 1px solid #dddddd">
            <ul class="textareaall">
              <li
                style="width: 50%"
                v-for="(item, index) in allTextarea"
                :key="index"
              >
                <h5 style="padding: 0.5vh; height: 5vh">
                  国家：{{ item.country }}---问题类型：{{ item.problem_type }}
                </h5>
                <div
                  style="width: 100%; height: 13vh; overflow-y: auto"
                  @click="alldiv(index)"
                  class="allDiv"
                >
                  {{ item.email_content }}
                </div>
              </li>
            </ul>
            <input
              type="checkbox"
              class="elseCheckbox"
              @click="elseCheckbox()"
            /><textarea
              name=""
              id=""
              cols="40"
              rows="8"
              class="elseTexarea"
              @blur="elseCheckbox()"
            ></textarea>
          </div>
          <div style="flex: 5">
            <el-input
              type="textarea"
              placeholder="模板翻译内容"
              v-model="replyTranslateChinese2"
              maxlength="9999"
              show-word-limit
              style="width: 35vw; margin: 0 0 0 4.5vw"
              :rows="7"
              readonly
            >
            </el-input>
          </div>
        </div>
        <div style="margin: 2vh 0 0 10vh; display: flex">
          <div style="flex: 4">
            <div style="margin-bottom: 2vh">
              问题类型：
              <el-autocomplete
                class="inline-input"
                v-model="replyProblemType"
                :fetch-suggestions="querySearch"
                placeholder="请输入内容"
                style="width: 20vw"
              ></el-autocomplete>

              <!-- <el-input
                placeholder="请选择模板获取"
                v-model="replyProblemType"
                style="width: 20vw"
              ></el-input> -->
            </div>
            <div style="max-height: 15vh; overflow-y: auto">
              缺件及其数量
              <ul style="width: 100%" class="lessul">
                <li
                  style="width: 100%"
                  v-for="(item, index) in partData"
                  :key="index"
                >
                  <input
                    type="checkbox"
                    class="lackCheck"
                    @change="lackCheckChange"
                  /><span class="lessspan">{{ item.part_code }}</span
                  ><input
                    type="number"
                    value="0"
                    disabled
                    style="width: 40%; height: 2.5vh"
                    class="lackDo"
                  />
                </li>
              </ul>
            </div>
            <div style="max-height: 15vh; overflow-y: auto; margin-top: 2vh">
              破损及其数量
              <ul style="width: 100%" class="badul">
                <li
                  style="width: 100%"
                  v-for="(item, index) in partData"
                  :key="index"
                >
                  <input
                    type="checkbox"
                    @change="damageCheckChange"
                    class="damagedCheck"
                  /><span class="damagespan">{{ item.part_code }}</span
                  ><input
                    type="number"
                    value="0"
                    min="0"
                    disabled
                    style="width: 40%; height: 2.5vh"
                    class="damagedDo"
                  />
                </li>
              </ul>
            </div>
            <div>
              相关操作:<select
                v-model="replyassociatedOperation"
                style="width: 15vw; height: 3vh"
              >
                <option value="发新品">发新品</option>
                <option value="退全款">退全款</option>
                <option value="部分退款">部分退款</option>
                <option value="退货">退货</option>
              </select>
            </div>
            <div>
              客户评级:<select
                v-model="replywhiteOrBlack"
                style="width: 15vw; height: 3vh"
              >
                <option value="白名单">白名单</option>
                <option value="黑名单">黑名单</option>
              </select>
            </div>
            <div>
              <!-- :on-remove="handleRemove" -->
              <el-upload
                class="upload-demo"
                drag
                ref="upload"
                action="123"
                multiple
                :before-upload="beforeUpload"
                :http-request="uploadFile"
              >
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">
                  将文件拖到此处，或
                  <em>点击上传</em>
                </div>
                <!-- <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div> -->
              </el-upload>
            </div>
          </div>
          <div style="flex: 6; margin-left: 14vw"></div>
        </div>
        <div style="margin: 2vh 0 0 10vh; display: flex; margin-bottom: 5vh">
          <div style="flex: 1">
            <el-button type="primary" @click="submitEmail()">提 交</el-button>
          </div>
          <div style="flex: 1">
            <el-button type="warning" @click="replacement()">重 置</el-button>
          </div>
          <div style="flex: 1">
            <el-button type="danger" @click="cancel()">取 消</el-button>
          </div>
          <div style="flex: 6"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      // 输入及下拉
      restaurants: [],

      editArr: {
        replySite: true,
        replyCountry: true,
        replyPlatform: true,
        replyLanguage: true,
        replyGoodsNo: true
      }, // 是否可编辑
      replyGoodsNo: "", //回复邮件商品及其数量
      replyOrderNo: "", //回复邮件订单编号
      replySite: "", //回复邮件站点
      replyCountry: "", //回复邮件国家
      replyLanguage: "", //回复邮件语言
      replyBuysText: "", //买家消息内容
      replyTranslateChinese: "", //翻译成中文
      replyTranslateChinese2: "", //翻译成中文2
      replyProblemType: "", //回复邮件问题类型
      replyassociatedOperation: "", //回复文件相关操作
      replywhiteOrBlack: "", //是否黑白名单
      replysku: "", //回复邮件SKU
      replyPlatform: "", //回复邮件平台
      replyProductName: "", //提交产品名
      replyProductCode: "", //提交产品编号
      submitWarehouseCode: "", //提交仓库号

      total: 0,
      page: 1,
      leftNavData: [], //侧边导航栏数据
      firstNav: "", //一级导航
      twiceNav: "", //二级导航
      threeNav: "", //三级导航
      tableData: [], //表格数据
      allTextarea: [], //框框所有textarea
      productDo: "", //商品编码
      partData: [], //缺件和损坏
      interpretBtn: false, //匹配并翻译是否能按判断

      replyUploadPeople: "", //上传人

      fileData: [] //文件上传
    };
  },
  methods: {
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    querySearch(queryString, cb) {
      // restaurants 列表 [{value:'xx'}]
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    uploadFile() {},
    beforeUpload(file) {
      this.fileData.push(file);
    },
    getDefaultData() {
      //获取默认数据
      axios({
        url: this._url2 + "server/customer_management/email_manage",
        method: "get",
        params: {
          page: 1,
          type: "站内"
        }
      }).then(res => {
        console.log(res);
        // this.leftNavData = res.data.left_data_dict;
        this.tableData = res.data.re_data;
        this.total = res.data.count[0].count / 5;
        console.log(this.tableData);
      });
    },
    NavClick(index, index2, item3) {
      //点击三级导航栏事件
      this.firstNav = index;
      this.twiceNav = index2;
      this.threeNav = item3;
      axios({
        url: this._url2 + "server/customer_management/email_manage",
        method: "get",
        params: {
          page: 1,
          type: "站内",
          platform: this.firstNav,
          country: this.twiceNav,
          site: this.threeNav
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.re_data;
        this.$nextTick(() => {
          $(".BuyerNewsTable>tbody").scrollTop(0);
        });
        this.total = res.data.count[0].count / 5;
        console.log(this.tableData);
        this.page = 1;
      });
    },
    fileSubmit(ev) {
      //文件上传的函数
      let file = ev.target.file;
      let formData = new FormData();
      for (let item of file) {
        formData.append("", item);
      }
    },
    damageCheckChange() {
      //判断是否损坏件
      let damagedCheck = document.querySelectorAll(".damagedCheck");
      let damagedDo = document.querySelectorAll(".damagedDo");
      for (let i = 0; i < damagedCheck.length; i++) {
        if (damagedCheck[i].checked == true) {
          damagedDo[i].removeAttribute("disabled");
        } else {
          damagedDo[i].setAttribute("disabled", "disabled");
        }
      }
    },
    elseCheckbox() {
      //判断有没有勾选
      if (document.querySelector(".elseCheckbox").checked == true) {
        let str = document.querySelector(".elseTexarea").value;
        if (!str.trim().length) {
          console.log(str.trim());
          return;
        }
        console.log("没有输入值");
        axios({
          url: this._url2 + "server/customer_management/re_message_translate/",
          method: "get",
          params: {
            content: str
          }
        }).then(res => {
          console.log(res.data.re_content);
          //["你好", "en"]
          if (res.data.re_content.length > 0) {
            this.replyTranslateChinese2 = res.data.re_content[0];
          } else {
            this.replyTranslateChinese2 = "";
          }
          console.log(res);
        });
        this.replyProblemType = "";
        let allDiv = document.querySelectorAll(".allDiv");
        for (let i = 0; i < allDiv.length; i++) {
          allDiv[i].style.backgroundColor = "";
        }

        console.log("判断有误勾选", str);
      }
    },
    lackCheckChange() {
      //判断是否缺件
      let lackCheck = document.querySelectorAll(".lackCheck");
      let lackDo = document.querySelectorAll(".lackDo");
      for (let i = 0; i < lackCheck.length; i++) {
        if (lackCheck[i].checked == true) {
          lackDo[i].removeAttribute("disabled");
        } else {
          lackDo[i].setAttribute("disabled", "disabled");
        }
      }
    },
    getDataById() {
      //根据订单编号获取数据
      axios({
        url: this._url2 + "server/customer_management/order_num_inquire/",
        method: "get",
        params: {
          order_num: this.replyOrderNo
        }
      }).then(res => {
        // 清除数据
        this.replySite = "";
        this.replyCountry = "";
        this.replyPlatform = "";
        this.replyLanguage = "";
        this.replyGoodsNo = "";
        console.log(res, 88880);
        // 还原为只读
        for (let key in this.editArr) {
          this.editArr[key] = true;
        }
        if (res.data.data.length > 0) {
          this.replySite = res.data.data[0].site;
          // this.replySite =
          //   this.replySite.charAt(0).toUpperCase() + this.replySite.slice(1);
          for (let key in res.data.country_dict) {
            console.log(res.data.data[0]);
            if (res.data.data[0].country_code == key) {
              this.replyCountry = res.data.country_dict[key][0];
              this.replyLanguage = res.data.country_dict[key][1];
              this.productDo = res.data.data[0].product_code;
              this.replyGoodsNo =
                res.data.data[0].product_name +
                "        数量:" +
                res.data.data[0].quantity;
              this.replysku = res.data.data[0].sku;
              this.replyPlatform = res.data.data[0].platform;
              this.replyProductName = res.data.data[0].product_name;
              this.replyProductCode = res.data.data[0].product_code;
              this.partData = res.data.commodity_data;
              // this.submitWarehouseCode = res.data.data[0].warehouse_code;

              // 清空数据
              this.reflash();
            }
          }
        }
        // 设置是否只读
        if (!this.replySite) {
          this.editArr["replySite"] = false;
        }
        if (!this.replyCountry) {
          this.editArr["replyCountry"] = false;
        }
        if (!this.replyPlatform) {
          this.editArr["replyPlatform"] = false;
        }
        if (!this.replyLanguage) {
          this.editArr["replyLanguage"] = false;
        }
        if (!this.replyGoodsNo) {
          this.editArr["replyGoodsNo"] = false;
        }
      });
    },
    reflash() {
      this.replyassociatedOperation = ""; // 相关操作
      this.replywhiteOrBlack = ""; //  客户评级
      // this.replygoodORbad = ""; // 是否获得好评
      // this.replyGoodsNo = ""; //商品及其数量
      this.replyTranslateChinese = ""; //买家消息内容翻译
      this.replyTranslateChinese2 = ""; //模板内容翻译

      this.replyProblemType = ""; // 问题类型
      this.replyBuysText = ""; // 买家消息内容
      this.allTextarea = []; //清空模板
      //缺件及其数量 //破损及其数量
      document.querySelector(".elseTexarea").value = "";
    },
    interpret() {
      //匹配并翻译
      this.interpretBtn = true;
      let formData = new FormData();
      formData.append("platform", this.replyPlatform);
      formData.append("country", this.replyCountry);
      formData.append("language", this.replyLanguage);
      formData.append("content", this.replyBuysText);
      formData.append("product_code", this.productDo);
      this.$axios
        .post(
          this._url2 +
            "server/customer_management/translate_semantic_analysis/",
          formData
        )
        .then(res => {
          this.replyTranslateChinese = res.data.data;
          console.log(res, 99);
          this.allTextarea = res.data.tem_data;
          console.log(this.allTextarea, 123);
          this.interpretBtn = false;
          // 处理问题类型下拉列表
          let problemList = res.data.re_problem_data || [];
          this.restaurants = [];
          for (let key in problemList) {
            let item = {};
            let value = problemList[key];
            item.value = value.problem_type;
            this.restaurants.push(item);
          }
        });
    },
    submitEmail() {
      console.log("问题类型", this.replyProblemType);
      let flag = 0; //判断勾选的input框是否有值
      let damagedDo = document.querySelectorAll(".damagedDo");
      let damagedCheck = document.querySelectorAll(".damagedCheck");
      let damagespan = document.querySelectorAll(".damagespan");
      let lackCheck = document.querySelectorAll(".lackCheck");
      let lackDo = document.querySelectorAll(".lackDo");
      let lessspan = document.querySelectorAll(".lessspan");
      let demo = [];
      let demo2 = [];
      let str = "";
      for (let i = 0; i < damagedCheck.length; i++) {
        if (damagedCheck[i].checked == true) {
          let demo3 = [];
          demo3.push(damagespan[i].innerHTML);
          demo3.push(damagedDo[i].value);
          demo.push(demo3);
          if (damagedDo[i].value == "") {
            flag++;
          }
        }
      }
      for (let i = 0; i < lackCheck.length; i++) {
        if (lackCheck[i].checked == true) {
          let demo4 = [];
          demo4.push(lessspan[i].innerHTML);
          demo4.push(lackDo[i].value);
          demo2.push(demo4);
          if (lackDo[i].value == "") {
            flag++;
          }
        }
      }
      console.log(demo, demo2, 123);
      if (flag >= 1) {
        alert("请把勾选的缺件破损零件数量填上或者不勾选");
      } else {
        let allDiv = document.querySelectorAll(".allDiv");
        for (let i = 0; i < allDiv.length; i++) {
          if (allDiv[i].style.backgroundColor == "skyblue") {
            str = allDiv[i].innerHTML;
          }
        }
        if (document.querySelector(".elseCheckbox").checked == true) {
          str = document.querySelector(".elseTexarea").value;
        }
        if (str === "" || this.replyCountry == "") {
          alert("订单编号和回复邮件模板不能为空！");
        } else {
          let formData = new FormData();
          console.log(
            this.submitWarehouseCode,
            this.replyProductCode,
            this.submitWarehouseCode,
            this.replyOrderNo,
            this.replyProblemType,
            this.replyassociatedOperation,
            this.replywhiteOrBlack,
            demo2,
            demo,
            str,
            this.replyBuysText,
            this.replyTranslateChinese2,
            this.replyTranslateChinese,
            this.replysku
          );

          formData.append("warehouse_code", this.submitWarehouseCode);
          formData.append("product_code", this.replyProductCode);
          formData.append("product_name", this.replyGoodsNo);
          formData.append("order_number", this.replyOrderNo);
          formData.append("problem_type", this.replyProblemType);
          formData.append("related_operations", this.replyassociatedOperation);
          formData.append("customer_status", this.replywhiteOrBlack);
          formData.append("lack_part", demo2);
          formData.append("damaged", demo);
          formData.append("email_template", str);
          formData.append("email_content", this.replyBuysText);
          formData.append("translation_template", this.replyTranslateChinese2);
          formData.append("customer_translation", this.replyTranslateChinese);
          formData.append("sku", this.replysku);
          formData.append("type", "站内");
          formData.append("upload_people", this.replyUploadPeople);
          for (let i = 0; i < this.fileData.length; i++) {
            formData.append("files", this.fileData[i]);
            console.log("文件", this.fileData[i]);
          }
          let str2 =
            this.replyPlatform.charAt(0).toUpperCase() +
            this.replyPlatform.slice(1); //第一个大写
          formData.append("platform", str2);
          formData.append("country", this.replyCountry);
          formData.append("site", this.replySite);

          console.log(formData, 123123);
          this.$axios
            .post(
              this._url2 + "server/customer_management/customer_reply_email/",
              formData
            )
            .then(res => {
              if (res.data.code == 200) {
                alert("提交成功");
              }
            });
        }
      }
    },
    alldiv(index) {
      //模板变色
      if (document.querySelector(".elseCheckbox").checked == false) {
        let allDiv = document.querySelectorAll(".allDiv");
        for (let i = 0; i < allDiv.length; i++) {
          allDiv[i].style.backgroundColor = "";
        }
        allDiv[index].style.backgroundColor = "skyblue";
        this.replyTranslateChinese2 = this.allTextarea[index].email_translation;
        this.replyProblemType = this.allTextarea[index].problem_type;
      }
    },
    showMail() {
      //亮出下方
      document.querySelector(".bottomBox").style.display = "block";
    },

    replacement() {
      //重置按钮
      this.replyBuysText = "";
      this.replyTranslateChinese = "";
      this.replyassociatedOperation = "";
      this.replywhiteOrBlack = "";
      this.replyGoodsNo = "";
      this.replyOrderNo = "";
      this.replySite = "";
      this.replyCountry = "";
      this.replyLanguage = "";
      this.allTextarea = [];
      this.replyTranslateChinese2 = "";
      this.replyPlatform = "";
      document.querySelector(".elseTexarea").value = "";
    },
    cancel() {
      //取消按钮
      document.querySelector(".bottomBox").style.display = "none";
    },
    handleCurrentChange(newPage) {
      // 页码切换会触发此函数
      if (this.firstNav == "") {
        this.firstNav = null;
      }
      if (this.twiceNav == "") {
        this.twiceNav = null;
      }
      if (this.threeNav == "") {
        this.threeNav = null;
      }
      this.page = newPage;
      axios({
        url: this._url2 + "server/customer_management/email_manage",
        method: "get",
        params: {
          page: this.page,
          type: "站内",
          platform: this.firstNav,
          country: this.twiceNav,
          site: this.threeNav
        }
      }).then(res => {
        console.log(res);
        this.tableData = res.data.re_data;

        this.$nextTick(() => {
          $(".BuyerNewsTable>tbody").scrollTop(0);
        });
        this.total = res.data.count[0].count / 5;
        console.log(this.tableData);
      });
      if (this.firstNav == null) {
        this.firstNav = "";
      }
      if (this.twiceNav == null) {
        this.twiceNav = "";
      }
      if (this.threeNav == null) {
        this.threeNav = "";
      }
    }
  },
  created() {
    this.getDefaultData();
    axios({
      //左侧导航栏（权限用）
      url: this._url2 + "server/personnel_management/area_sign2/",
      method: "get"
    }).then(res => {
      console.log(res, 5);
      this.leftNavData = res.data.data;
    });
  },

  watch: {
    replyTranslateChinese2: function() {
      if (document.querySelector(".elseCheckbox").checked == true) {
      }
    }
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
    this.replyUploadPeople = sessionStorage.getItem("user_name");
  }
};
</script>

<style scoped>
.el-upload__input {
  display: none !important;
}
table {
  /*width: 84vw;*/
  margin: 10px 0px;
  border-collapse: collapse;
}

tbody {
  display: block;
  max-height: 60vh;
  width: fit-content;
  overflow-y: auto;
  border-bottom: 1px #e5e5e5 solid;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 6vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table > tbody > tr:hover {
  background: #eaf7e0;
}

table > thead > tr > th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  height: 2.5vh;
  line-height: 2.5vh;
}

table > tbody > tr > td {
  font-size: 0.7rem;
  color: #000;
  height: 2.5vh;
  line-height: 2.5vh;
}

/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #dddddd;
  background-clip: padding-box;
  min-height: 5vh;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #bbb;
}

.bottomBox {
  position: relative;
  margin: 5vh 5vh 0 0;
  border: 2px solid #dddddd;
  border-radius: 10px;
  display: none;
  background: rgba(255, 255, 255, 0.9);
}

textarea {
  height: 15vh !important;
}

.textareaall li,
.badul li,
.lessul li {
  border: 1px #dddddd solid;
  float: left;
}

.leftNav {
  width: 12vw;
  height: 100vh;
  position: fixed;
  left: 0;
}

table tr th:nth-child(1),
table tr td:nth-child(1),
table tr th:nth-child(2),
table tr td:nth-child(2),
table tr th:nth-child(3),
table tr td:nth-child(3) {
  width: 7vw;
}

table tr th:nth-child(4),
table tr td:nth-child(4),
table tr th:nth-child(6),
table tr td:nth-child(6) {
  width: 23vw;
}

table tr th:nth-child(5),
table tr td:nth-child(5) {
  width: 10vw;
}

.elseTexarea {
  width: 47%;
}
</style>



